@import '../../../sass/config';

.searchContainer{

  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 50px; 

  .itemTitle{
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    margin-top: 10px;
    .title{
      font-size: 35px;
    }
  }

  .itemContainer{
    display: flex;
    width: 100%;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .noResult{
    text-align: center;
  }
}  
